<template>
    <div>
        <section class="shop">
            <nav @click="GoBack()">
                <van-icon name="arrow-left" />
            </nav>
            <header>
                <img class="img1" :src="'//elm.cangdu.org/img/'+shopimg" alt="">
                <section class="head">
                    <a href="">
                        <section class="cc1">
                            <img :src="'//elm.cangdu.org/img/'+shopimg" alt="" class="img2">
                        </section>
                        <section class="cc2">
                            <h4>{{header.name}}</h4>
                            <p class="p1">{{header.category}}</p>
                            <p class="p2">{{header.address}}</p>
                        </section>
                        <section class="cc3">
                            <van-icon name="arrow" />
                        </section>
                    </a>
                </section>
            </header>
            <section class="goods">
                <div>
                    <span >商品</span>
                </div>
                <div>
                    <span>评价</span>
                </div>
            </section>
            <section class="nav">
                <section class="content-goods">
                    <section class="nav-left">
                        <ul>
                            <li v-for="(v,i) in goods_left" :class='changeColor==i?"active":""' :key="i" @click="changeCss(i)">{{v.name}}</li>
                        </ul>
                    </section>
                    <section class="nav-right" ref="navright">
                        <ul>
                            <li  v-for="(v,i) in goods_left" :key="i" class="li">
                                <header>
                                    <section>
                                        <strong>{{v.name}}</strong>
                                        <span>{{v.description}}</span>
                                    </section>
                                    <span>...</span>
                                </header>
                                <section class="shu" v-for="(l,o) in v.foods" :key="o">
                                    <div>
                                        <section class="ss1">
                                            <img :src="`//elm.cangdu.org/img/`+l.image_path" alt="">
                                        </section>
                                        <section class="ss2">
                                            <h3>
                                               <strong>{{l.name}}</strong> 
                                            </h3>
                                            <p class="p1">{{l.description}}</p>
                                            <p class="p2">{{l.tips}}</p>
                                        </section>
                                    </div>
                                    <footer class="shu-di">
                                    <section class="a1">
                                        <span class="span1">￥</span>
                                        <span class="span2">20</span>
                                    </section>
                                    <section class="a2">
                                        <section class="jj" @click="addQQ($event,l.specfoods[0])">
                                            <van-icon name="add" />
                                            <div></div>
                                        </section>
                                    </section>
                                </footer>
                                </section>
                                
                            </li>
                        </ul>
                    </section>
                </section>
                <section class="addGwc">
                    <section class="gwc-left" @click="tan()">
                        <div class="gwcImg sc" :id='sum>0?"aaa":""' >
                            <span v-if="sum>0">{{sum}}</span>
                            <van-icon name="shopping-cart"  />
                        </div>
                        <div class="gwcZi">
                            <div class="yy">￥{{jiage}}</div>
                            <div class="ss">配送费￥5</div>
                        </div>
                    </section>
                    <router-link to="/order">
                    <section class="gwc-right">
                        <p>结算</p>
                    </section>
                    </router-link>
                </section>
                <section class="listGwc" v-if="show">
                    <header>
                        <h4>
                            购物车
                        </h4>
                        <div @click="delGwc()">
                            <van-icon name="delete" />
                            <span>清空</span>
                        </div>
                    </header>
                    <section class="sssss">
                        <ul class="uuu">
                            <li v-for="(v,i) in gwc" :key="i">
                                <div class="dd1">
                                    <p>{{v.name}}</p>
                                </div>
                                <div class="dd2">
                                    <span class="ss1">￥</span>
                                    <span class="ss2">{{v.num*v.price}}</span>
                                </div>
                                <section class="dd3">
                                    <span class="ss1"><van-icon name="close" @click="jian(v.item_id)"/></span>
                                    <span class="ss2">{{v.num}}</span>
                                    <span class="ss3"><van-icon name="add-o" @click="jia(v.item_id)"/></span>

                                </section>
                            </li>
                        </ul>
                    </section>
                </section>
                <div class="zg" v-if="zg"  @click="tan()"></div>
            </section>

        </section>
    </div>
</template>

<script>
   import the from '../shop/theball/theBall'
   import '../shop/theball/theBall.css'

    export default {


        data(){
            return{
                shopimg:"",
                goods_left:[],
                header:[],
                changeColor:0,
                goodsColor:true,
                countHight:[],
                show:false,
                zg:false,
               
            }
        },
        methods:{
             GoBack() {
                this.$router.go(-1);
            },
            tan(){
                // alert(111)
                this.show=!this.show
                if(this.show==true){
                    this.zg=true
                }else{
                    this.zg=false
                }
            },
            changeCss(i){
                this.changeColor=i
                this.$refs.navright.scrollTop=this.countHight[i]
                // console.log(i,this.changeColor)
            },
            _countHight(){          
                let scroll=this.$refs.navright.getElementsByClassName("li")
                console.log(scroll);
                
                let height=0
                this.countHight.push(height)
                for(let i in scroll){
                    height+=scroll[i].clientHeight
                this.countHight.push(height)

                }
                console.log(this.countHight);
                // for()
            },
            _scrollHight(){
                let scrollY=this.$refs.navright.scrollTop
                // console.log(scrollY);
                for(let i=0;i<this.countHight.length;i++){
                    let height1=this.countHight[i]
                    let height2=this.countHight[i+1]
                    if(scrollY>=height1&&scrollY<height2){
                        // console.log(i);
                        
                        this.changeColor=i
                    }
                }                
                
            },
            addQQ(e,val){
                this.$store.commit("add",val)
                the(e)
               
            },
            jian(id){
                 this.$store.commit("jian",id)
            },
            jia(id){
                 this.$store.commit("jia",id)
            },
            delGwc(){
                this.$store.commit("qkgwc")
            }

        },
        mounted() {
            //图片
            // console.log(this.$route.query)
            var img=this.$route.query.img;
            this.shopimg=img;

            //内容
            var id=this.$route.query.id;
            this.$http.get("http://elm.cangdu.org/shopping/v2/menu?restaurant_id="+id+"").then((res)=>{
                // console.log(res);
                this.goods_left=res.data
                this.$nextTick(()=>{
                this._countHight()
            })
            });
            //头
            this.$http.get("http://elm.cangdu.org/shopping/restaurant/"+id+"?latitude=22.96286&longitude=113.28904&extras[]=activities&extras[]=album&extras[]=license&extras[]=identification&extras[]=statistics").then((res)=>{
                // console.log(res);
                this.header=res.data
            })
             this.$refs.navright.addEventListener("scroll",this._scrollHight)
            
            
        },
        computed: {
            gwc(){
                return this.$store.state.gwc;
            },
             sum(){
                return this.$store.getters.sum;
            },
            jiage(){
                return this.$store.getters.jiage;
            }
        },
    }
</script>

